body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url('https://i0.wp.com/cdn-prod.medicalnewstoday.com/content/images/articles/322/322605/football-helmet-and-ball-on-grass-or-turf.jpg?w=1155&h=1541');
  /* Set a specified height, or the minimum height for the background image */
  min-height: 500px;

  /* Set background image to fixed (don't scroll along with the page) */
  background-attachment: fixed;

  /* Center the background image */
  background-position: center;

  /* Set the background image to no repeat */
  background-repeat: no-repeat;

  /* Scale the background image to be as large as possible */
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.matchupsTable {
  border-collapse: collapse;
  width: 100%;
  opacity: 0.9;
  background-color: antiquewhite;
}

.matchupsTable #week{
  font-weight: 800;
}

.matchupsTable td{
  border: 2px solid black;
  padding: 8px;
}
.matchupsTable th{
  border: 3px solid black;
  padding: 8px;
}

.matchupsTable tr{
  background-color: #7C987B;
}

.matchupsTable tr:nth-child(10n-4){
  background-color: #E4A596;
}
.matchupsTable tr:nth-child(10n-3){
  background-color: #E4A596;
}
.matchupsTable tr:nth-child(10n-2){
  background-color: #E4A596;
}
.matchupsTable tr:nth-child(10n-1){
  background-color: #E4A596;
}
.matchupsTable tr:nth-child(10n){
  background-color: #E4A596;
}


.matchupsTable .winner {
  font-weight: 700;
}

.matchupsTable tr:hover {background-color: #9B5F50;}

.matchupsTable th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: #C28374;
  color: white;
}


.matchup-table-outcome-winner{
  background-color: darkseagreen;
}

.matchup-table-outcome-loser{
  background-color: lightcoral;
}

.standings-table {
  border-collapse: collapse;
  width: 80%;
  opacity: 0.9;
  background-color: antiquewhite;
  display: inline-table;
}

.standings-table td{
  border: 3px solid black;
  vertical-align: middle !important;
  padding: 8px;
}
.standings-table th{
  border: 3px solid black;
  vertical-align: middle !important;
  padding: 8px;
}

/* .standings-table tr:nth-child(5n){border-bottom: 3px solid black;} */

.standings-table .winner {
  font-weight: 700;
}
.standings-table-logo{
  vertical-align: middle !important;
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.standings-table .name-and-logo{
  display: flex;
  align-items: center;
  /* margin: 6px 0px; */
}

.standings-table tr:hover {background-color: #ddd;}

.standings-table th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: green;
  vertical-align: middle !important;
  color: white;
}

